<template>
	<el-dialog title="" ref="modal" :visible.sync="modalVisible" 
		:close-on-click-modal="true"
		class="add-device-form">

		<el-form ref="form" :model="form" :rules="uploadformrules">
			
		<el-form-item class="file-wrap" prop="file">

			<el-upload class='ensure' 
				ref="uploadForm"
				name="uploadFile"
				:data="dataParams"
				:action="uploadUrl"  
				:auto-upload="false"				
				:on-success="uploadSuc"
				:on-change="onchange"
				:before-upload="beforeUploadAdv"
				>
				<el-button size="small" type="primary"></el-button>
				<span slot="tip" class="el-upload__tip">jpg/pngmp4</span>				
	
			</el-upload>
		</el-form-item>

		<el-form-item label="" prop="title" :label-width='labelwidth'>
			<el-input type="text" v-model="form.title"  ></el-input>
		</el-form-item>

		<el-form-item label="" prop="expire" :label-width='labelwidth'>
			<el-date-picker
				v-model="form.expire"
				type="date"
				value-format="timestamp"
				placeholder="">
			</el-date-picker>
		</el-form-item>
		
		<!--el-form-item label="" prop="adType" :label-width='labelwidth'>  
            <el-select clearable  v-model="form.adType" placeholder="">
				<el-option label="" value="1" selected></el-option>
				<el-option label="" value="2"></el-option>
			</el-select>
		</el-form-item-->

		<el-form-item label="" prop="groups" :label-width='labelwidth'>
				<el-select clearable v-model="form.groups" multiple placeholder="">
					<el-option v-for="item in groups" :label="item.groupName" :value="item.groupId"></el-option>
				</el-select>
		</el-form-item>
		
		<el-form-item class="subbut">
			<el-button size="small" @click="modalVisible = false"></el-button>
			<el-button size="small" type="primary" 
				@click="uploadSubmit"></el-button>
		</el-form-item >
		
	</el-form>
	</el-dialog>
</template>

<script>
	/* eslint-disable */
	
	import { mapGetters } from 'vuex';	
	import { AdminAPI } from '../../api';
	

	export default {
		
		data: function() {
			return { // add				
				form: {                   
                    file: '',
					title: '',
					adType: '',
					groups:[],
					expire:'',
                },
                dataParams:{title:'', adType:'',groups:[],expire:''},               
				uploadUrl:AdminAPI.uploadAdvUrl,
				modalVisible: true,
				submitLoading: false,
				disabled:true,
				versions:['1','2'],				
				fileList:[],
				labelwidth:'90px',
				uploadformrules: {
						title: [
							{ required: true, message: '', trigger: 'change' },
						],
						//adType:[
						//	{ required: true, message: '', trigger: 'change' },
						//],
						groups:[
							{ required: true, message: '', trigger: 'change' },
						],
						expire:[
							{ required: true, message: '', trigger: 'change' },
						]

				},
			}
		},
		computed: {
			
		},
		methods: {		
			beforeUploadAdv(file){
				if(['image/jpeg','image/png','video/mp4'].indexOf(file.type) == -1){
					this.$message.error(' jpg/png, mp4 !');
					return false;
				}        		
				if (file.size / 1024 / 1024 > 100) {
					this.$message.error('100MB');
					return false;
				}

				if(['image/jpeg','image/png'].indexOf(file.type) >= 0){
					this.dataParams.adType = 1;
				}else{
					this.dataParams.adType = 2;
				}
			},
			uploadSuc(res, file, fileList){
				if(res.status === 0){
					this.modalVisible = false;
					console.log('suc',res)
					this.$message({
						message: '!',
						type: 'success'
					});
					this.$emit('data', {result: 'success'});
				}else{
					this.$message.error('!'+res.msg);
				}
			},
			onchange(file,fileList){				
				this.fileList = fileList;				
			},

			uploadSubmit() {
				 this.$refs.form.validate((valid) => {
				 	this.dataParams.title = this.form.title;
					//this.dataParams.adType = this.form.adType;
					this.dataParams.groups = this.form.groups;
					this.dataParams.expire = this.form.expire;
					 if(this.fileList.length > 1){
						this.$message.error('');
						return;
					 }
					this.$refs.uploadForm.submit();
					
				 });
			},			
		},
		
		mounted() {
			
		}
	}
</script>

<style scoped lang="scss">
	
	.el-dialog{
		width:400px;
	}

	.el-upload__tip {
		display: inline-block;
		padding-left: 20px;
	}
	.subbut{
		text-align: right;
		margin: 22px 0 0 0;
	}
	.el-input,.el-select{
		width:400px !important;
	}
</style>