<template>
	<el-row>
		<el-col :span="16">
			<el-form ref="form" :model="form" label-width="14em" :rules="formRules">
			
				
				<el-form-item  size="small" prop="alarmPhone" label="">
					<el-input v-model="form.alarmPhone"></el-input>
				</el-form-item>

				<el-form-item  size="small" prop="errorTimes" label="">
					<el-input v-model="form.errorTimes"></el-input>
				</el-form-item>
				
				<el-form-item>
					<el-button type="primary" size="small" @click="onSubmit"></el-button>
					<el-button size="small"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
	</el-row>
</template>

<script>
	import { AdminAPI } from '../../api';
	export default {
		data() {			
			return {				
				form: {
					alarmPhone:'',
					errorTimes: '',					
				},				
				
				alarmPhone:'',
				errorTimes:'',
				formRules: {	
					alarmPhone:[
						{ required: true, message: '', trigger: 'blur' },
					],		
					errorTimes: [
						{ required: true, message: '', trigger: 'blur' },
					],				
				}
			}
		},
		methods: {
			onSubmit() {							
				this.$refs.form.validate(valid => {
					if(valid) {
						AdminAPI.updateSetting({
							...this.form
						}).then(({
							data
						}) => {
							if(data.status === 0) {
								this.$message({
									type: 'success',
									message: '!',
								})
							} else {
								this.$message.error(`:${data.msg}!`);
							}
						}).catch(() => {
							this.$message.error(`!`);
						});
					}
				})
			},
			loadData() {
				AdminAPI.getSetting().then(({
					data
				}) => {
					if(data.status === 0) {	
						if(data.data !== null)					
							this.form = data.data;				
						
					} else {
						this.$message.error(`:${data.msg}!`);
					}
				}).catch(() => {
					this.$message.error(`!`);
				});
			}
		},
		mounted() {
			this.loadData();
		}
	}
</script>